import React, {useState} from 'react';
import {Card} from '../../components';
import './style.scss';
import {basicURL, toast, parseQuery} from "../../utils";
import {recharge} from "../../service";
import {connect, Input, MaskLoading} from "../../components"
import BigNumber from "bignumber.js"
let packageJson = require('../../../package.json');
let canUseContractRaw = false;
window.demos.canIUse('contractRaw').then((data) => {
    canUseContractRaw = data;
})

function Home({address, balance, btcLight, genesis, status, stop, surplus, PMR, level, levelApply, rechargePrice, getIdentity, code, maxbtc}) {
    const invite = code || parseQuery.getQuery('invite') || '';
    const [rechargeCount, setRechargeCount] = useState(''); //兑换（花费）BTC数量
    const [invitedAddress, setInvitedAddress] = useState(invite); //邀请地址
    // const [invitedAddress, setInvitedAddress] = useState('TcASfmyoFiNobNaCHeiPMkWgGmovoxk1sqk'); //邀请地址
    const [isLoading, setIsLoading] = useState(false); //开通loading

    function expDOS() {
        let result = '--';
        let btc = rechargeCount.trim() ? Number(rechargeCount) : -1;
        if(Number(level) >= 0 && btc >= 0 ){
            btc = btc * 1e8;
            // console.log(level, levelApply, btc);
            function calc() {
                let total = 0;
                let apply = levelApply;
                let maxLevel = 10246;
                for (let i=level; true; i++) {
                    let j = i;
                    if ( i > maxLevel) j = maxLevel;
                    let dos = 4 * (j+1) * 1e8;

                    if(apply !== 0) {
                        if (apply > dos) {
                            apply -= dos;
                            continue;
                        }
                        dos -= apply;
                    }

                    let price = 1e-5 + 1e-7*j;
                    let use = price * dos;

                    if (btc < use) {
                        let num = btc / price;
                        total += Math.floor(num);
                        // if (level === i) {
                        //     return total;
                        // }
                        return total;
                    }

                    btc = btc - use;
                    total += dos;
                }
            }

            let res = calc() * 1e-8;
            // console.log(res);
            result = `约${res} DOS`;
        }

        return result;
    }

    const copyAddress = () => {
        if (!genesis) return;
        try {
            const input = document.getElementById('genesis-container');
            input.select();
            document.execCommand('Copy');
            toast('复制成功');
        } catch (e) {
            toast('复制失败');
        }
    };

    function submit () {
        if(status === 1){
            toast('已停盘');
            return
        }
        if (!canUseContractRaw) {
            toast('请更新app');
            return
        }
        let _value = Number(rechargeCount);
        let value = _value > 0 ? _value + '' : '';
        setRechargeCount(value);

        if (value === '') {
            toast('请填写花费BTC数量');
            return
        }

        if (maxbtc && value > maxbtc) {
            toast(`最多花费${maxbtc}BTC`);
            return
        }

        if (value * 1e8 > balance) {
            toast('余额不足');
            return
        }

        rechargeDOS(value);
    }

    function rechargeDOS (value) {
        let n_a = new BigNumber(value);
        let n_b = new BigNumber(1e8);
        let n = n_a.times(n_b).toString();
        window.demos.contractRaw("pay", btcLight, [{type: "uint256", value: n - 0}, {type:"string", value: "[3]"}]).then(function (data){
            setIsLoading(true);
            recharge({
                address,
                addr: code || invitedAddress,
                rawtx: data
            }).then(function ({data}) {
                setIsLoading(false);

                if(data.ret) {
                    toast('兑换成功');
                    setRechargeCount('');
                    getIdentity(address);
                } else {
                    toast(data.data);
                }
            }, function (err) {
                setIsLoading(false);
                toast(err.toString(), 30000);
            })
        }, function (err) {
            toast(err.toString(), 30000);
        });
    }

    function changeAddress (e) {
        setInvitedAddress(typeof e === 'string' ? e : e.target.value.trim());
    }

    function scan () {
        window.demos.scanQRCode().then(function (data){
            let addr = data;
            if(data.includes('address=')) {
                addr = data.match(/address=(\w+)/)[1]
            }
            setInvitedAddress(addr);
        });
    }

    async function paste() {
        function focus () {
            document.getElementById('p-home-input').focus();
        }
        if(!invitedAddress) {
            try {
                const value = await window.demos.getClipboard();
                value ? setInvitedAddress(value) : focus();
            } catch (e) {
                focus();
            }
        } else {
            focus();
        }
    }

    function changeRechargeCount (e) {
        let value = typeof e === 'string' ? e : e.target.value;
        setRechargeCount(value);
    }

    function BalanceToFixed(n) {
        // return (n * 1e-8).toFixed(18).replace(/0*$/g,"")
        return n === 0 ? '0' : (new BigNumber(n).times(new BigNumber(1e-8))).toFixed(18).replace(/0*$/g,"");
        // return (n * 1e-8).toFixed(18).replace(/0*$/g,"")
    }

    return (
        <div className='p-home'>
            <Card className='list-item'>
                {/*<p className='p1'>剩余DOS: <i>{BalanceToFixed(surplus)}</i></p>*/}
                {/*<p className='p1'>BTC余额: <i>{BalanceToFixed(balance)}</i></p>*/}
                <p className='p1'>剩余DOS: <i>{BalanceToFixed(surplus)}</i></p>
                <p className='p1'>BTC余额: <i>{BalanceToFixed(balance)}</i></p>
                <p className='p2'>创世账户地址: <i>{genesis || '--'}</i></p>
                <div className='bottom'><button className='c-btn' onClick={copyAddress}>复制地址</button></div>
                <input
                    type="text"
                    value={genesis}
                    id="genesis-container"
                    readOnly
                    style={{ border: 0, padding: 0, position: 'fixed', left: '-100%' }}
                    onChange={() => {}}
                />
            </Card>
            <img src={`${basicURL.imgSrc}/metcalfe.png`} alt="btc" className='metcalfe'/>
            <div className='pmr'>PMR={PMR}</div>
            <div className='triangle'>
                <div><i>当前{level}层</i></div>
            </div>
            {status === 0
                ? <div className='status'>状态:正常</div>
                : <div className='status status-stop'>状态:停盘</div>
            }
            <div className='label'>
                <label>当前兑换价格: </label>
                <p>{rechargePrice} BTC</p>
            </div>
            <div className='label'>
                <label>距离停盘: </label>
                <p>{stop/1e8} DOS</p>
            </div>
            <div className='label'>
                <label>可兑换: </label>
                <p>{expDOS()}</p>
            </div>
            <div className='content'>
                <Input
                    type='text'
                    placeholder='花费BTC数量'
                    value={rechargeCount}
                    onChange={changeRechargeCount}
                    id='p-home-1'
                />
                {invite ? (
                    <Card className='invite'>推荐地址: {invite}</Card>
                ) : (
                    <div className='input'>
                        <div className='input-wrapper'>
                            <Input
                                type="text"
                                placeholder='推荐地址'
                                onChange={changeAddress}
                                value={invitedAddress}
                                id='p-home-input'/>
                            {/*<i onClick={paste} />*/}
                        </div>

                        {/*<img src={`${basicURL.imgSrc}/icon_scan.png`} alt="scan" onClick={scan}/>*/}
                    </div>
                )}
                {/*<input type="text" placeholder='推荐地址' value={invitedAddress}/>*/}
                {isLoading && <MaskLoading />}
                <button className='c-btn' onClick={submit} disabled={isLoading}>兑换</button>
            </div>
            <div className='package-json'>version: {packageJson.version}</div>
        </div>
    )
}

export default connect()(Home);
